import React from "react";
import { Button, Container } from "@mui/material";
import { Link } from "react-router-dom";
import "./style.css";

const About = () => {
  return (
    <Container className="about-wrapper">
      <h1 className="title">Eu desenvolvo solues.</h1>
      <p>Me chamo talo Simes, mas pode me chamar apenas de talo.</p>
      <p>
        Natural do estado do Maranho, graduado pela Universidade Federal do
        Maranho {"("}UFMA{")"} no curso de Cincia da Computao, venho atuando
        no desenvolvimento web Front-end desde 2021.
      </p>
      <p>
        Utilizando como principais tecnologias ReactJS, AngularJS, Javascript,
        Typescript, alm de HTML5 e CSS3, venho desenvolvendo aplicaes Web com
        foco em acessibilidade, praticidade e usabilidade para que todos os
        usurios possam ser includos e consigam utilizar as aplicaes de
        acordo com sua necessidade.
      </p>
      <p>
        Busco sempre eficincia e simplicidade na criao de projetos para que
        sua utilizao seja intuitiva, eficaz e prazerosa por parte dos
        usurios.
      </p>
      <Button
        className="about-button"
        component={Link}
        to="/projetos"
        variant="contained"        
      >
        Veja alguns de meus projetos
      </Button>
      <p>
        J trabalhei em projetos prprios e com times de desenvolvimento de
        mltiplos pases, sempre buscando integrao e agilidade para que os
        processos possam fluir com mais facilidade e velocidade.
      </p>
      <p>Tem interesse em criarmos um projeto juntos? </p>
      <Button
        className="about-button"
        component={Link}
        to="/contato"
        variant="contained"
      >
        Vamos conversar
      </Button>
    </Container>
  );
};

export default About;
